/* --------------------About Me--------------------- */
.photo-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 30px 0;
}
.profile-img {
    height: 33rem;
    width: 25rem;
    border-radius: 50%;
    padding: 0.3rem;
    border: 0.12rem solid var(--primary-orange);
}

.about-heading {
    color: var(--primary-orange);
    text-transform: uppercase;
}

.about-me__container {
    height: 16rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.about-me__container-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.about-me__container p {
    padding: 2rem 0;
    font-size: 1.2rem;
}
@media (max-width: 540px) {
    .about-me__container {
        height: 15rem;
    }
}

@media (max-width: 468px) {
    .profile-img {
        height: 19rem;
        width: 15rem;
    }
    .about-me__container {
        height: 15rem;
    }
}
